.gpt3__blog{
    display: flex;
    flex-direction: column;
}
.gpt3__blog-heading{
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}
.gpt3__blog-heading h1{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px; /* 120.968% */
    letter-spacing: -2.48px;
}
.gpt3__blog-container{
    display: flex;
    flex-direction: row;
}
.gpt3__blog-container_groupA{
    flex: 0.75;
    margin-right: 2rem;
}
.gpt3__blog-container_groupB{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:2rem;
}
@media screen and (max-width:990px){
    .gpt3__blog-container{
        flex-direction: column-reverse;
    }
    .gpt3__blog-container_groupA{
        margin-top: 2rem;
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article{
        width: 48%;
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article-image{
        height: 250px;
    }
}
@media screen and (max-width:990px){
    .gpt3__blog-container_groupB{
        grid-template-columns: repeat(1,1fr);
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article{
        width: 100%;
    }
    .gpt3__blog-heading h1{
        font-size: 48px;
        line-height: 45px;
        margin-bottom: 2rem;
    }
}
@media screen and (max-width:550px){
    .gpt3__blog-heading h1{
        font-size: 34px;
        line-height: 42px;
        /* margin-bottom: 2rem; */
    }
}
